
input[type="radio"],
input[type="checkbox"]{
  height: auto;
}

body{
  font-size: 15px;
  color: $body-color;
}
a{
  text-decoration: none;
}
ul{
  padding: 0;
  margin: 0;
  list-style-type: none;
  li{
    list-style: none;
    margin: 0;
  }
}
.directorist-wrapper{
  div{
    font-size: 15px;
  }
  article{
    margin-bottom: 0;
    border: 0;
    padding-bottom: 0;
  }
  
}
