/* Range Slider */
.atbdp-range-slider-wrapper{
  display: flex;
  width: 100%;
  align-items: center;
}
.atbdp-range-slider-wrapper > span{
  color: $dark;
}
.atbdp-range-slider-wrapper > div{
  flex: 1;
  padding: 0 15px;
}
.atbdp-range-slider-wrapper .atbd-current-value{
  flex: 1;
  padding: 0 15px !important;
}
.atbdp-range-slider-wrapper.atbdprs-widget{
  display: block;
  margin: 0;
}
.atbdp-range-slider-wrapper.atbdprs-widget > div{
  padding: 0;
}
.atbdp-range-slider-wrapper.atbdprs-widget .atbd-current-value{
  margin-bottom: 15px !important;
  padding: 0 !important;
}
.atbdp-range-slider-wrapper.atbdp-range-slider-widget{
  display: block;
  padding-bottom: 9px;
  >div{
    padding: 0;
  }
  .atbdp-range-slider-title{
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 5px;
  }
}

.atbd-slide1{
  width: 1.2em;
  height: 1.2em;
  position: absolute;
  top: -2px;
  left: 0;
  margin-top: -5px;
  cursor: pointer;
  border-radius: 50%;
}
.atbd-slide2{
  width: 30px;
  height: 10px;
  position: absolute;
  top: 50%;
  right: 0;
  margin-top: -5px;
  cursor: pointer;
  border-radius: 10px
}
.atbd-m-device{
  transform: scaleY(2);
}
.atbd-m-device2{
  transform: scaleX(2);
}
p.atbd-max {
  float: right;
}
p.atbd-min {
  float: left;
}
.atbd-child{
  height: 4px;
  border-radius: 2px;
  background: #444752;
  position: absolute;
  top: 0;
  /*rtl:ignore*/
  left: 0;
}

// Directorist Range Slider
.directorist_slider-range{
  position: relative;
  .directorist_range-bar{
      position: absolute;
      z-index: 1;
      left: 1px;
      top: 6px;
      width: 100%;
      height: 4px;
      border-radius: 12px;
      background-color: #E3E6EF;
      overflow: hidden;
      .directorist_range-fill{
          display: block;
          width: 0;
          height: 100%;
          background-color: #3E62F5;
      }
  }
  .directorist_slider-input{
      position: relative;
      z-index: 2;
      -webkit-appearance: none;
      width: 100%;
      height: 4px;
      border-radius: 12px;
      outline: none;
      background-color: transparent;
      &::-webkit-slider-thumb{
          -webkit-appearance: none;
          width: 20px;
          height: 20px;
          background-color: #ffffff;
          border-radius: 50%;
          cursor: pointer;
          outline: none;
          box-shadow: 0 0 0 0 rgba(98,0,238,.1);
          transition: .3s ease-in-out;
          border: 4px solid #3E62F5;
          position: relative;
          top: -4px;
      }
  }
}